<template>
    <div id="dashboard">
        <ul class="collection with-header">
            <li class="collection-header"><h4>Projects</h4></li>
            <li v-for="project in projects" v-bind:key="project.project_id" class="collection-item">
                <div class="chip">{{project.project_desc}}</div>
                <router-link class="secondary-content left"
                     v-bind:to="{name: 'view-project', params: {project_id: project.project_id}}">
                    تفاصيل
                </router-link>
            </li>
        </ul>

        <div class="fixed-action-btn">
            <router-link to="/new" class="btn-floating btn-large red">
                اضافة
            </router-link>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'dashboard',
    data() {
        return {
            projects : []
        }
    },
    created() {
        db.collection('projects').get().then(querySnapshot =>  {
            querySnapshot.forEach(doc => {
                const data = {
                    'id': doc.id,
                    'project_id': doc.data().project_id,
                    'project_desc': doc.data().project_desc,
                    'project_info': doc.data().project_info,
                    // 'to_start': doc.data().to_start,
                    // 'to_end': doc.data().to_end
                }
                this.projects.push(data)
            });
        })
    }
}
</script>


