<template>
    <div id="new-project">
        <h3>New Project</h3>
        <div class="row">
            <form @submit.prevent="saveProject" class="s12">
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="project_id" required>
                        <label>Project Number</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="project_desc" required>
                        <label>Project Description</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="project_info" required>
                        <label>Project Information</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="first_company" required>
                        <label>Company Name</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="second_company">
                        <label>Company Name</label>
                    </div>
                </div>
                <button type="submit" class="btn">Submit</button>
                <router-link to="/" class="btn grey">Cancel</router-link>
            </form>
        </div>
        
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'new-project',
    data() {
        return {
            project_id: null,
            project_desc: null,
            project_info: null,
            first_company: null,
            second_company: null
        }
    },
    methods: {
        saveProject() {
            db.collection('projects').add({
                project_id: this.project_id,
                project_desc: this.project_desc,
                project_info: this.project_info,
                first_company: this.first_company,
                second_company: this.second_company
            })
            .then(docRef => {
                this.$router.push('/')
            })
            .catch(error => console.log(err))
        }
    }
}
</script>


