<template>
    <div id="edit-project">
        <h3>Edit Project</h3>
        <div class="row">
            <form @submit.prevent="updateProject" class="s12">
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="project_id" required disabled>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="project_desc" required>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="project_info" required>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="first_company" required>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="second_company">
                    </div>
                </div>
                <button type="submit" class="btn">Submit</button>
                <router-link to="/" class="btn grey">Cancel</router-link>
            </form>
        </div>
    </div>
</template>

<script>
import db from './firebaseInit'
export default {
    name: 'edit-project',
    data() {
        return {
            project_id: null,
            project_desc: null,
            project_info: null,
            first_company: null,
            second_company: null
        }
    },
    beforeRouteEnter(to, from, next) {
        db.collection('projects').where('project_id', '==', to.params.project_id).get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                next(vm => {
                    vm.project_id = doc.data().project_id
                    vm.project_desc = doc.data().project_desc
                    vm.project_info = doc.data().project_info
                    vm.first_company = doc.data().first_company
                    vm.second_company = doc.data().second_company
                })
            })
        })
    },
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData() {
            db.collection('projects').where('project_id', '==', this.$route.params.project_id).get()
                .then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                        this.project_id = doc.data().project_id
                        this.project_desc = doc.data().project_desc
                        this.project_info = doc.data().project_info
                        this.first_company = doc.data().first_company
                        this.second_company = doc.data().second_company
                    })
            })
        },
        updateProject() {
            db.collection('projects').where('project_id', '==', this.$route.params.project_id).get()
                .then(querySnapshot => {
                    querySnapshot.forEach(doc => {
                        doc.ref.update({
                            project_id: this.project_id,
                            project_desc: this.project_desc,
                            project_info: this.project_info,
                            first_company: this.first_company,
                            second_company: this.second_company
                        })
                        .then(() => {
                            this.$router.push({name: 'view-project', params:{project_id: this.project_id}})
                        })
                    })
            })
        }
    }
}
</script>


